
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Divider from 'vux/src/components/divider/index.vue'


export default {
name: 'PersonalCenter',
components: {
ViewBox,
XHeader,
Group,
Divider,
Cell
},
data () {
return {
person: {
account: ''
}
}
},
created: function () {
this.person.account = this.$db.get('User').account
},
methods: {
backHome () {
this.$emit('close-center')
},
logout () {
if (this.$appConfig.deployAPP) {
let message = '?'
let title = ''
let buttonLabels = ','
const _that = this
navigator.notification.confirm(message, (buttonText) => {
console.log(buttonText)
if (buttonText === 2) {
console.log('')
window.localStorage.clear()
window.sessionStorage.clear()
_that.$router.replace({'name': 'login'})
}
}, title, buttonLabels)
} else {
window.localStorage.clear()
window.sessionStorage.clear()
this.$router.replace({'name': 'login'})
}
}
}
}



