
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Qrcode from 'vux/src/components/qrcode/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'

import html2canvas from 'html2canvas'

export default {
directives: {
TransferDom
},
name: 'invite',
components: {
ViewBox,
XHeader,
Popup,
Qrcode
},
data () {
return {
inviteCode: this.$db.get('User').invitationCode,
dataURL: '',
showMyInfo: false,
showCard: false,
clipButton: {
text: '',
isClick: false,
clickedCss: {
'background-color': 'grey'
}
}
}
},
methods: {
clickClip () {
this.clipButton.isClick = true
this.clipButton.text = ''
console.log(this.$refs.qrcodeImg.imgData)

},
gotoInvitePage () {
window.location.href = 'www.baidu.com'
},
createCard () {
this.showMyInfo = true
this.showCard = true







},
toImage () {












document.createElement('canvas').toDataURL()
var c = document.createElement('canvas')
var data = c.toDataURL('image/png')
console.log(data.indexOf('data:image/png') === 0)
html2canvas(this.$refs.imageWrapper, {
backgroundColor: null
}).then((canvas) => {
let scrBase64 = canvas.toDataURL('image/jpeg')
scrBase64 = scrBase64.split(',')[1]
console.log('22222222')
console.log(scrBase64)















})
}
}
}



